/**
 * WordPress dependencies
 */
import { __ } from "@wordpress/i18n";
/**
 * Reservation status mappings
 */
export const getReservationStatus = (status) => {
    const statusMappings = {
        pending: {
            label: __("Pending", "wpcafe"),
            color: "bg-yellow-500",
        },
        confirmed: {
            label: __("Confirmed", "wpcafe"),
            color: "bg-green-500",
        },
        cancelled: {
            label: __("Cancelled", "wpcafe"),
            color: "bg-red-500",
        },
        completed: {
            label: __("Completed", "wpcafe"),
            color: "bg-blue-500",
        },
        rejected: {
            label: __("Rejected", "wpcafe"),
            color: "bg-gray-500",
        },
    };
    return (statusMappings[status] || {
        label: status || __("Unknown", "wpcafe"),
        color: "bg-gray-400",
    });
};
