import { jsxs as _jsxs, jsx as _jsx } from "react/jsx-runtime";
/**
 * WordPress Dependencies
 */
import { __ } from "@wordpress/i18n";
/**
 * Internal Dependencies
 */
import { StatusBadge } from "@/common/components/StatusBadge";
import { getOrderStatus } from "../../constant";
import { AdminPrice } from "@/common/components";
/**
 * Order table column definitions
 */
export const ORDER_TABLE_COLUMNS = [
    {
        key: "orderId",
        title: __("Order Id", "wpcafe"),
        render: (_, record) => (_jsxs("span", { className: "font-medium text-gray-900", children: ["#", record.order_id] })),
    },
    {
        key: "customer",
        title: __("Customer", "wpcafe"),
        dataIndex: "customer_name",
    },
    {
        key: "items",
        title: __("Items", "wpcafe"),
        dataIndex: "items_count",
    },
    {
        key: "total",
        title: __("Total", "wpcafe"),
        render: (_, record) => (_jsx(AdminPrice, { className: "font-medium text-gray-900", price: record.total_amount })),
    },
    {
        key: "status",
        title: __("Status", "wpcafe"),
        render: (_, record) => {
            const statusInfo = getOrderStatus(record.status);
            return (_jsx(StatusBadge, { label: statusInfo.label, color: statusInfo.color }));
        },
    },
];
